.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

table, th, td {
   border: 1px solid black;
}

table {
  border-collapse: collapse;
  table-layout: fixed ;
  width: 50% ;
  height: 50%;
}

th, td {
  padding: 1rem;
  text-align: left;
}

img {
  width: 100%;
}